import {KeyValueObject} from '../../common/common.models';

// 'any' type is needed here for backward compatibility
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export type RxTypeaheadOption = string | KeyValueObject | any;

export interface RxTypeaheadResultTemplateContext {
  /**
   * Your typeahead result item.
   */
  result: RxTypeaheadOption;

  /**
   * Search term from the `<input>` used to get current result.
   */
  term: string;
}

export interface RxTypeaheadSelectItemEvent {
  /**
   * The item from the result list about to be selected.
   */
  item: RxTypeaheadOption;

  /**
   * Calling this function will prevent item selection from happening.
   */
  preventDefault: () => void;
}
